  //      ,
  //      
const formatTime = (date: string) => {
  //     
  let today = new Date().getDate();
  //   
  let ms = Date.parse(date);
  //     
  let orderDate = new Date(ms);
  //  
  let hr: number | string = orderDate.getHours();
  if (hr < 10) hr = "0" + hr;
  // 
  let min: number | string  = orderDate.getMinutes();
  if (min < 10) min = "0" + min;
  //  
  const orderTime = `${hr}:${min} i-GMT+3`;

  let formatData = "";
  switch (orderDate.getDate()) {
    case today:
      formatData = `, ${orderTime}`;
      break;
    case today - 1:
      formatData = `, ${orderTime}`;
      break;
    case today - (today - orderDate.getDate()):
      formatData = `${today - orderDate.getDate()}   , ${orderTime}`;
      break;
    default:
      formatData = "";
      break;
  }
  return formatData;
};
export default formatTime;
