import { Switch, Route } from "react-router-dom";
import { useDispatch, useSelector } from "../../utils/hooks";
import { useEffect, useCallback } from "react";
import { useLocation, useHistory } from "react-router-dom";
import {
  LoginPage,
  HomePage,
  RegisterPage,
  ForgotPasswordPage,
  ResetPasswordPage,
  ProfilePage,
  FeedPage,
  OrderInfoPage,
  OrderHistoryPage,
  NotFoundPage,
} from "../../pages";
import AppHeader from "../app-header/app-header";
import Modal from "../modal/modal";
import IngredientInfo from "../ingredient-info/ingredient-info";
import OrderInfo from "../order-info/order-info";
import { getItems } from "../../services/actions/ingredient";
import { checkAuth } from "../../services/actions/user";
import { ProtectedRoute } from "../protected-route/protected-route";
//   Location ,      Switch
import { Location } from "history"

function App() {
  const dispatch = useDispatch();
  const { isAuthChecked } = useSelector((store) => store.user);

 type TLocation = {
    from: Location;
    background?: Location;
    pathname: string;
}
  const location = useLocation<TLocation>();
  //  ?.  
  //   undefined,    ?.  undefined  null.
  const background = location?.state?.background;
  //let background = location.state && location.state.background;
  const history = useHistory();
  // from  ,     
  const from = location?.state?.from;

  //        
  useEffect(() => {
    dispatch(getItems());
    //   ,   
    dispatch(checkAuth());
  }, [dispatch]);

  // ,     WebSocket
  const { orders, wsRequest, wsFailed } = useSelector((store) => store.ws);

  //      ,        id .
  //    .         .
  // Dispatch  .

  const closeModal = useCallback(
    (path) => {
      history.push(path);
    },
    [history]
  );

  return (
    <>
      {!isAuthChecked && (
        <p
          className="text text_type_main-medium"
          style={{ textAlign: "center" }}
        >
           ...
        </p>
      )}
      {isAuthChecked && (
        <>
          <AppHeader />
          <Switch location={background || location}>
            {/*  */}
            <ProtectedRoute anonymous={true} path="/login">
              <LoginPage />
            </ProtectedRoute>
            {/* ,   */}
            <Route path="/" exact={true}>
              <HomePage />{" "}
            </Route>
            {/*   */}
            <Route path="/feed" exact={true}>
              <FeedPage />
            </Route>
            {/*      */}
            <Route path="/feed/:id" exact={true} children={<OrderInfoPage />} />
            {/*  */}
            <ProtectedRoute anonymous={true} path="/register" exact={true}>
              <RegisterPage />
            </ProtectedRoute>
            {/*   */}
            <ProtectedRoute
              anonymous={true}
              path="/forgot-password"
              exact={true}
            >
              <ForgotPasswordPage />
            </ProtectedRoute>
            {/*   */}
            <ProtectedRoute
              anonymous={true}
              path="/reset-password"
              exact={true}
            >
              <ResetPasswordPage />
            </ProtectedRoute>
            {/*      */}
            <Route
              path="/ingredients/:id"
              exact={true}
              children={<IngredientInfo />}
            />
            {/*  */}
            <ProtectedRoute path="/profile" exact={true}>
              <ProfilePage />
            </ProtectedRoute>
             {/*    */}
            <ProtectedRoute path="/profile/orders" exact={true}>
              <OrderHistoryPage />
            </ProtectedRoute>
             {/*      */}
            <ProtectedRoute path="/profile/orders/:id" exact={true}>
              <OrderInfoPage userToken />
            </ProtectedRoute>
            {/*    */}
            <Route path="*">
              <NotFoundPage />
            </Route>
          </Switch>
          {/*        */}
          {background && (
            <Route
              path="/ingredients/:id"
              children={
                <Modal
                  onClose={() => {
                    closeModal("/");
                  }}
                >
                  <IngredientInfo />
                </Modal>
              }
            />
          )}
          {/*        */}
          {background && (
            <Route
              path={`${from}/:id`}
              children={
                <Modal
                  onClose={() => {
                    closeModal(from);
                  }}
                >
                  {!wsFailed && wsRequest && (
                    <p className="text text_type_main-medium">
                         ...
                    </p>
                  )}
                  {orders && <OrderInfo />}
                </Modal>
              }
            />
          )}
        </>
      )}
    </>
  );
}
//

export default App;
